import Swal from "sweetalert2";
export function swal(text) {
    Swal.fire({
      title: 'Thnh cng',
      type: 'success',
      icon: 'success'
    });
  }
 export function swalErr(text) {
    Swal.fire({
      title: 'Tht bi',
      type: 'success',
      icon: 'error'
    });
  }
