import React, { useRef, useState, useEffect } from 'react'
import ReactExport from "react-export-excel";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;

export default (data) => {
    return (
        <ExcelFile
            filename={'Berber_manager'}
            element={<button className="btn btn-info">Export Excel</button>}>
            <ExcelSheet data={data.dataset} name="Leaves">
                <ExcelColumn label="M n" value="_id" />
                <ExcelColumn label="Tn sn phm" value="nameProduct" />
                <ExcelColumn label="Tn khch hng" value="fullName" />
                <ExcelColumn label="S in thoi" value="phoneNumber" />
                <ExcelColumn label="a ch" value="address" />
                <ExcelColumn label="n gi" value="priceProduct" />
                <ExcelColumn label="S lng" value="amountProduct" />
                <ExcelColumn label="Thnh tin" value="amountProduct * priceProduct" />
            </ExcelSheet>
        </ExcelFile>
    )
}